import React from "react";
import { graphql } from "gatsby";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import Layout from "@components/layout";
import Seo from "@components/seo";
import BreadCrumb from "@components/common/breadcrumb";
import NFTDevelopmentScreen from "@screens/nft-development-company/nft-development-screen";

const NFTDevelopmentCompany = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "NFT Marketplace Development Company", link: null },
  ];
  return (
    <Layout db={db}>
      <BreadCrumb items={breadcrumbLinks} />
      <main>
        <NFTDevelopmentScreen db={db} />
      </main>
    </Layout>
  );
};

export const Head = (props) => {
  const { data } = props;
  const fileUrl =
    data?.nftMarketPlaceImg?.childImageSharp?.gatsbyImageData?.images?.fallback?.src;
  return (
    <Seo
      title="NFT Marketplace Development Company | Justtry Technologies"
      metaTitle="NFT Marketplace Development Company | NFT Software Development Services"
      description="Justtry is a top-end NFT Marketplace Development Company that offers the best NFT Marketplace Development solutions and Services to Create an NFT Marketplace."
      image={fileUrl}
    ></Seo>
  );
};
export default NFTDevelopmentCompany;

export const queryData = graphql`
  query MyQuery {
    nftMarketPlaceImg: file(relativePath: { eq: "nft-marketplace.png" }) {
      childImageSharp {
        gatsbyImageData
      }
    }
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
    allImageSharp {
      edges {
        node {
          gatsbyImageData
          id
        }
      }
    }
  }
`;
